const app = getApp();
import helper from '../common/helper.js';
import uniDrawer from '@/components/uni-drawer.vue'
export default {
	data() {
		return {
			indicatorDots: true,
			Inv: 0,
			list: [],
			keywords: '',
			navList: [],
			tabType: 'is_new',
			page_size: '10',
			page: '1',
			imgUrl: '',
			navLeft: [],
			// navReft: [],
			active: '0',
			activeStyle: {
				color: this.activeTextColor,
				backgroundColor: this.activeBackgroundColor
			},
			activeHead: '0',
			showLeft: false,
			classType: '0',
			category_id: '',
			letfNavChild: [],
			token: ''
		};
	},
	components: {uniDrawer},
	onLoad() {
		this.token = uni.getStorageSync('token')
		this.getNav();
		// this.getTab();
		this.imgUrl = helper.imgUrl;
		this.getNavLeft()
	},
	methods: {
		// 轮播跳转
		navsHead(e) {
			console.log(e)
			uni.navigateTo({
				url: `/pages/allProduct?id=${e.currentTarget.dataset.id}&title=${e.currentTarget.dataset.title}`
			});
		},
		// 左边导航
		leftNav(index,id) {
			this.active = index;
			// this.showLeft = true;
			this.category_id = id;
			this.page = '1';
			// this.page = '1'
			this.list = [];
