		getNavLeft(){
			uni.request({
				url: `${helper.requestUrl}/holes/categories`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.navLeft = res.data.data;
						this.category_id = res.data.data[0].id
						this.page = '1';
						this.list = [];
						this.getTab();
						// let arr = res.data.data;
