		// 				// 	icon: 'none'
		// 				// });
		// 			}
		// 		}
		// 	});
		// },
		closeDrawer() {
			this.showLeft = false
		},
		clickItem(category_id){
			this.showLeft = false
			this.category_id = category_id
			this.page = '1';
			this.list = []
			this.getTab()
		},
		// 右边头部导航
		// headNav(index,id) {
		// 	console.log(index,id);
		// 	this.activeHead = index;
		// 	this.classType = id
		// 	this.list = [];
		// 	this.page = '1';
		// 	this.getTab()
		// },
		changeTab(e) {
			console.log(e);
			this.Inv = e;
			this.page = '1';
			if (this.Inv == 0) {
				// 最新
				this.tabType = 'is_new';
			} else if (this.Inv == 1) {
				// 热门
				this.tabType = 'is_hot';
			} else if (this.Inv == 3) {
				// 推荐
				this.tabType = 'is_romend';
			}
			this.list = [];
			this.getTab();
		},
		getSearch() {
			uni.navigateTo({
				url: '/pages/searchNetloan'
			});
		},
		//轮播导航
		getNav() {
			uni.request({
				url: `${helper.requestUrl}/holes/banks`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res,'左边导航数据');
					if (res.data.status_code == 200) {
						let arr = res.data.data;
						let result = [];
						let num = 10;
						for (let i = 0; i < arr.length; i += num) {
							result.push(arr.slice(i, i + num));
						}
						console.log(result);
						this.navList = result;
						// this.getReftNav()
						// this.page = '1'
						// this.list = [];
						// this.getTab();
						
					} else {
					}
				}
			});
		},
		// 最新
		getTab() {
			uni.request({
				url: `${helper.requestUrl}/holes/index-recommends`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				data: {
					type: this.tabType,
					category_id: this.category_id,
					// class_id: this.classType,
					page_size: this.page_size,
					page: this.page
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.list = this.list.concat(res.data.data);
					} else {

					}
				}
			});
		},
		goProduct(e) {
			console.log(e,'*********************************');
			let id = e.currentTarget.dataset.id;
			uni.navigateTo({
				url: `/pages/productDetail?id=${id}`
			});
		},
		onReachBottom() {
			this.page++;
			this.getTab();
		}
	}
};
